.main {
  background-color: #ffffff;
  width: 500px;
  height: 500px;
  padding: 35px;
  border-radius: 10px;
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  align-items: center;
  overflow: hidden;
}

.dice {
  width: 100%;
  display: grid;
  grid-template: auto auto / repeat(5, 1fr);
  gap: 10px;
  align-items: center;
  justify-items: center;
}

@media screen and (max-width: 520px) {
  .main {
    width: 90%;
    padding: 10px;
  }
}

@media screen and (max-width: 300px) {
  .main {
    padding: 5px;
  }
}
