.win {
  position: absolute;
  top: -50px;
  left: 50%;
  transform: translateX(-50%);
  width: 140px;
  height: 50px;
  background-color: #5035ff;
  color: white;
  font-weight: 700;
  font-size: 1.5rem;
  text-align: center;
  line-height: 50px;
  border-radius: 5px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.6);
  transition: 0.2s ease-in-out;
}

.show {
  top: 50px;
}
