.die {
  width: 50px;
  height: 50px;
  border-radius: 10px;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.4);
  text-align: center;
  line-height: 50px;
  font-size: 1.5rem;
  font-weight: 700;
  color: #2b283a;
  cursor: pointer;
  user-select: none;
  transition: 0.2s;
}

.held {
  background-color: #59e391;
}

@media screen and (max-width: 550px) {
  .die {
    width: 40px;
    height: 40px;
    line-height: 40px;
    font-size: 1.4rem;
  }
}
